.root {
	color: #000000;
	font-size: 16px;
	line-height: 24px;
}

.root,
.root * {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.content {
	background-color: #5c6c7c;
	color: #ffffff;
	display: inline-block;
	padding: 8px;
}

.bottom .content {
	margin: 8px 0 0;
	-webkit-transform: translate(-50%, calc(100% + 8px));
	        transform: translate(-50%, calc(100% + 8px));
}

.bottom .content::before {
	border-left: 8px solid transparent;
	border-right: 8px solid transparent;
	border-bottom: 8px solid #5c6c7c;
	content: "";
	position: absolute;
	top: -8px;
	left: 50%;
	margin-left: -8px;
}

.top .content {
	margin: 0 0 8px;
	-webkit-transform: translate(-50%, calc(-100% - 8px));
	        transform: translate(-50%, calc(-100% - 8px));
}

.top .content::before {
	border-left: 8px solid transparent;
	border-right: 8px solid transparent;
	border-top: 8px solid #5c6c7c;
	content: "";
	position: absolute;
	bottom: -8px;
	left: 50%;
	margin-left: -8px;
}

.left .content {
	margin: 0 8px 0 0;
	-webkit-transform: translate(calc(-100% - 8px), -50%);
	        transform: translate(calc(-100% - 8px), -50%);
}

.left .content::before {
	border-top: 8px solid transparent;
	border-bottom: 8px solid transparent;
	border-left: 8px solid #5c6c7c;
	content: "";
	position: absolute;
	right: -8px;
	top: 50%;
	margin-top: -8px;
}

.right .content {
	margin: 0 0 0 8px;
	-webkit-transform: translate(calc(100% + 8px), -50%);
	        transform: translate(calc(100% + 8px), -50%);
}

.right .content::before {
	border-top: 8px solid transparent;
	border-bottom: 8px solid transparent;
	border-right: 8px solid #5c6c7c;
	content: "";
	position: absolute;
	left: -8px;
	top: 50%;
	margin-top: -8px;
}



