}

.root {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	color: #000000;
	font-size: 16px;
	line-height: 24px;
}

.root * {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.underlayVisible {
	background: rgba(0, 0, 0, 0.5);
	z-index: 400;
}

.pane {
	background: #ffffff;
	border: var(--ui-border-width) solid #d6dde2;
	-webkit-box-shadow: 0 9px 16px 0 rgba(0, 0, 0, 0.5);
	        box-shadow: 0 9px 16px 0 rgba(0, 0, 0, 0.5);
	z-index: 401;
}

.content {
	padding: 16px;
}

.title {
	border-bottom: var(--ui-border-width) solid #d6dde2;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	-webkit-box-flex: 0;
	    -ms-flex: 0 0 auto;
	        flex: 0 0 auto;
	font-size: 20px;
	height: 56px;
	padding: 16px;
	position: relative;
	text-align: left;
}

.close {
	background: none;
	border: none;
	cursor: pointer;
	font-size: 0;
	padding: 8px;
	position: absolute;
	right: 8px;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
}

.close i {
	font-size: 20px;
}

.left {
	-webkit-transform: translateX(calc(-100% - 10px));
	        transform: translateX(calc(-100% - 10px));
}

.right {
	-webkit-transform: translateX(calc(100% + 10px));
	        transform: translateX(calc(100% + 10px));
}

.top {
	-webkit-transform: translateY(calc(-100% - 20px));
	        transform: translateY(calc(-100% - 20px));
}

.bottom {
	-webkit-transform: translateY(calc(100% + 10px));
	        transform: translateY(calc(100% + 10px));
}

.slideIn,
.slideOut {
	-webkit-transition: -webkit-transform ease-in-out var(--ui-dpd-1);
	transition: -webkit-transform ease-in-out var(--ui-dpd-1);
	transition: transform ease-in-out var(--ui-dpd-1);
	transition: transform ease-in-out var(--ui-dpd-1), -webkit-transform ease-in-out var(--ui-dpd-1);
}

.open {
	-webkit-transform: translateX(0%);
	        transform: translateX(0%);
}



